import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  Image,
  Text,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";
import Images_Icons from "../../constants/icons-images";
import { FC } from "react";
import { nanoid } from "@reduxjs/toolkit";

interface BreadcrumbsProps {
  breadcrumbs: {
    name?: string | undefined;
    isCurrenPage?: boolean | undefined;
    link?: string | undefined;
  };
}

const Breadcrumbs: FC<BreadcrumbsProps | any> = ({
  breadcrumbs,
}): JSX.Element => {
  return (
    <Breadcrumb
      spacing="1px"
      separator={<Image src={Images_Icons.breadcrumbArrow} />}
      mb={"20px"}
    >
      {breadcrumbs?.map(
        (e: { name: string; isCurrentPage: boolean; link: string }) => (
          <BreadcrumbItem key={nanoid()} isCurrentPage={e.isCurrentPage}>
            <BreadcrumbLink as={Link} to={e.link}>
              <Text fontSize={["14px", "14px", "14px", "14px", "14px"]}>
                {e.name}
              </Text>
            </BreadcrumbLink>
          </BreadcrumbItem>
        )
      )}
    </Breadcrumb>
  );
};

export default Breadcrumbs;
