import {
  Avatar,
  Button,
  Center,
  Divider,
  Flex,
  Heading,
  HStack,
  Icon,
  Select,
  SimpleGrid,
  Spacer,
  Spinner,
  Stack,
  Text,
  useToast,
  Modal,
  ModalContent,
  ModalOverlay,
  useDisclosure,
  Circle,
  Image,
  Wrap,
  Skeleton,
  SkeletonCircle,
} from "@chakra-ui/react";
import { FaAngleDown } from "react-icons/fa";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import Breadcrumbs from "../../../components/Breadcrumbs";
import DashboardProductItem from "../../../components/DashboardProductItem";
import Footer from "../../../components/Footer";
import MeProtocolButton from "../../../components/MeProtocolButton";
import CategoriesFilter from "../../../components/CategoriesFilter";
import BrandsFilters from "../../../components/BrandsFilter";
import SizesFilter from "../../../components/SizesFilter";
import PriceFilter from "../../../components/PriceFilter";
import SortFilter from "../../../components/SortFilters";
import { useDashboardStyles } from "../Dashboard/styles";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AuthReducerProps, ProductReducerProps } from "../../../interfaces";
import { RootState } from "../../../redux/store";
import { nanoid } from "@reduxjs/toolkit";
import Images_Icons from "../../../constants/icons-images";
import { ShopImageSvg } from "../../../assets/svg/svg";
import {
  useCheckBrandFollowQuery,
  useFollowBrandMutation,
  useGetSingleShopsQuery,
  useUnfollowBrandMutation,
} from "../../../redux/rtkQuery/api";
import NewDashboardNavBar from "../../../components/NewDashboardNavBar";
import MeProtocolToast from "../../../hooks/MeProtocolToast";
import { IoIosAdd, IoIosCheckmark } from "react-icons/io";
import { SendIconSvg, ReceiveIconSvg } from "../../../assets/iconsSvg/iconsSvg";
import { followBrandOptions, GLOBAL_PREFIX } from "../../../constants";
import Container from "../../../components/Container";
import { BASE_URL } from "../../../config";
import axios from "axios";
import { Dispatch } from "redux";
import moment from "moment";

const DashboardShopFilter = () => {
  const { root, loadMore } = useDashboardStyles();
  let toast = useToast();
  const { id } = useParams();
  const { state }: any = useLocation();
  const dispatch = useDispatch<Dispatch>();

  let navigate = useNavigate();
  const [followLoading, setFollowLoading] = useState<boolean>(false);
  const [tokenOverviewVisible, setTokenOverviewVisible] = useState<boolean>(false);
  const [dateRange, setDateRange] = useState<string>("Last 30 days");
  const [page, setPage] = useState<number>(1);
  const [loadedOffers, setLoadedOffers] = useState<Array<any>>([]);
  const [reachedEnd, setReachedEnd] = useState<boolean>(false);
  const [totalPages, setTotalPages] = useState<number>(1);
  const [offersFetching, setOffersFetching] = useState<boolean>(false);
  const [brandOffers, setBrandOffers] = useState<any>([]);
  const [selectedOfferOption, setSelectedOfferOption] = useState<number | null>(null);

  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);
  const { isFollowingBrand, isFollowingBrand_loading }: ProductReducerProps = useSelector(
    (state: RootState) => state.productReducer
  );

  const [followBrand] = useFollowBrandMutation();
  const [unfollowBrand] = useUnfollowBrandMutation();

  const {
    isOpen: followingModalIsOpen,
    onOpen: followingModalOnOpen,
    onClose: followingModalOnClose,
  } = useDisclosure();

  const {
    data: singleBrand,
    isLoading,
    error,
  } = useGetSingleShopsQuery(
    { id },
    {
      refetchOnFocus: true,
      skip: !id,
    }
  );

  // console.log(, error, "POPOIH");

  const {
    data: brandFollowed,
    isLoading: brandFollowedLoading,
    refetch: brandFollowedRefresh,
  }: any = useCheckBrandFollowQuery(
    { brand_id: id as string },
    { refetchOnMountOrArgChange: true, skip: !token }
  );

  const {
    fontSmall,
    topCardStack,
    topCardHstack,
    imageSkel,
    tokenCenter,
    iconFlex,
    sendFlex,
    centerButton,
    textReceive,
    graphFlex,
    overviewFlex,
    modalContentFollow,
    overflowFlex,
    circleImage,
    circleImage2,
    followCenter,
    cancelCenter,
  } = useStyles();

  useEffect(() => {
    // if (token) dispatch(getUserInfo({ token }));

    // console.log(brandFollowed, "oooo");
    // dispatch(getBrandFollowCheck({ token, brand_id: state?.id }));

    GET_BRANDS_OFFERS();
  }, [page]);

  useEffect(() => {
    // GET_BRANDS_OFFERS();
  }, []);

  // --------------------------------------------------------------NEW FETCH IMPLEMENTATION---------------------------------------------------------------------------

  async function GET_BRANDS_OFFERS() {
    if (!id) return;
    setOffersFetching(true);
    setReachedEnd(false);
    try {
      const { data }: any = await axios.get(
        `${BASE_URL}/store/offer/?page=${page}&limit=4&brandId=${id}`
      );
      if (data?.data?.offers.length < 1) {
        setReachedEnd(true);
        MeProtocolToast(toast, "No offer available", 3000, "top-right");
      }

      // console.log(data?.data?.offers);
      setBrandOffers([...brandOffers, ...data?.data?.offers]);
      setTotalPages(data?.data?.pagination?.totalPage);
    } catch (e: any | unknown) {
      console.log(e, "GET_BRANDS_OFFERS_ERR");
    } finally {
      setOffersFetching(false);
    }
  }

  // useEffect(() => {
  //   LoadMore();
  // }, [page]);

  // function LoadMore() {
  //   setLoadingMore(true);

  //   try {
  //     if (data.data.offers.length < 1) {
  //       MeProtocolToast(toast, "No more offers available", 3000, "top-right");
  //       return;
  //     }
  //     setLoadedOffers([...loadedOffers, ...data.data.offers]);
  //   } catch (e) {
  //     console.log(e, "LoadMoreErr");
  //   } finally {
  //     setLoadingMore(false);
  //   }
  // }

  // -------------------------------------------------FOLLOW BRAND FN-----------------------------------------------
  async function FollowBrand() {
    if (!token) {
      navigate(`${GLOBAL_PREFIX}/login`);
    }
    setFollowLoading(true);
    await followBrand({ brand_id: id })
      .then(async (res: any) => {
        // console.log(res, "FollowBrandRes");
        await brandFollowedRefresh();

        if (res?.data?.statusCode === 200) {
          await brandFollowedRefresh();
          MeProtocolToast(toast, "Brand Followed", 5000);
        }
      })
      .catch((e: any | unknown) => {
        console.log(e, "FollowBrandErr");
      })
      .finally(() => {
        setFollowLoading(false);
      });
  }

  // -------------------------------------------------UNFOLLOW BRAND FN-----------------------------------------------
  async function UnFollowBrand() {
    if (!token) {
      navigate(`${GLOBAL_PREFIX}/login`);
    }
    setFollowLoading(true);
    await unfollowBrand({ brand_id: id })
      .then(async (res: any) => {
        await brandFollowedRefresh();

        if (res?.data?.statusCode === 200) {
          await brandFollowedRefresh();

          MeProtocolToast(toast, "Brand Unfollowed", 5000);
        }
      })
      .catch((e: any | unknown) => {
        console.log(e, "unFollowBrandERR");
      })
      .finally(() => {
        setFollowLoading(false);
      });
  }

  return (
    <Flex flexDir={"column"}>
      {/* ----------------------------------------------------------------------NAV BAR------------------------------------------------------------ */}
      {/* <DashboardNavBar /> */}
      <NewDashboardNavBar />
      <Container>
        <Stack {...root}>
          <Breadcrumbs
            breadcrumbs={[
              { name: "Home", link: `${GLOBAL_PREFIX}/dashboard` },
              {
                name: "Shop",
                link: `${GLOBAL_PREFIX}/dashboard/shop`,
              },
              {
                name: singleBrand?.data?.name?.replaceAll("_", " "),
                link: `${GLOBAL_PREFIX}/dashboard/shop/${id}`,
                isCurrentPage: true,
              },
            ]}
          />

          {/* ----------------------------------------------------------------------TOP CARD------------------------------------------------------------ */}

          <Stack minH={tokenOverviewVisible ? "800px" : "480px"} {...topCardStack}>
            <HStack {...topCardHstack}>
              <Stack w={"60%"} h={"100%"} p={"20px"}>
                {/* <Text fontSize={"32px"}>{singleBrand?.data?.brandName}</Text> */}
                <SkeletonCircle isLoaded={!isLoading} w="70px" h="70px">
                  <Image
                    src={singleBrand?.data?.logo}
                    objectFit={"contain"}
                    {...imageSkel}
                    fallbackSrc={Images_Icons.appLogoPlaceholder}
                    fallbackStrategy="beforeLoadOrError"
                  />
                </SkeletonCircle>
                <Skeleton isLoaded={!isLoading}>
                  <Text fontSize={"28px"} w={"75%"} noOfLines={2}>
                    {singleBrand?.data?.slogan}
                  </Text>
                </Skeleton>
                <Spacer />

                <Skeleton isLoaded={!isLoading}>
                  <Text fontSize={"16px"} noOfLines={3} w={"95%"}>
                    {singleBrand?.data?.description}
                  </Text>
                </Skeleton>

                <Flex>
                  {brandFollowed?.data ? (
                    <Flex className="followFlex">
                      <MeProtocolButton
                        name={"Following"}
                        isLoading={followLoading || brandFollowedLoading}
                        icon={<Icon w={"25px"} h={"25px"} as={IoIosCheckmark} />}
                        rest={{
                          w: "146px",
                          fontSize: "16px",
                          bg: "white",
                          borderWidth: 1,
                          borderColor: "dark",
                          color: "dark",
                          className: "following",
                        }}
                      />
                      <MeProtocolButton
                        name={"Unfollow"}
                        isLoading={followLoading || brandFollowedLoading}
                        rest={{
                          w: "146px",
                          fontSize: "16px",
                          bg: "red",
                          borderWidth: 1,
                          borderColor: "light",
                          color: "light",
                          className: "unfollow",
                        }}
                        onClick={UnFollowBrand}
                      />
                    </Flex>
                  ) : (
                    <MeProtocolButton
                      name={"Follow"}
                      isLoading={followLoading || brandFollowedLoading}
                      icon={<Icon w={"25px"} h={"25px"} as={IoIosAdd} />}
                      rest={{
                        w: "100px",
                        fontSize: "16px",
                      }}
                      // onClick={FollowBrand}
                      onClick={followingModalOnOpen}
                    />
                  )}
                </Flex>
              </Stack>
              <Flex w={"48%"} h={"100%"}>
                <Skeleton isLoaded={!isLoading}>
                  <ShopImageSvg
                    img={
                      singleBrand?.data?.banners
                        ? // ? singleBrand?.data?.cover_images[0]
                          singleBrand?.data?.banners[0]
                        : "https://cdn.cdnparenting.com/articles/2018/05/150311462-H.webp"
                    }
                  />
                </Skeleton>
              </Flex>
            </HStack>
            {/* ----------------------------------------------------------------------TOP CARD TOKEN INFO------------------------------------------------------------ */}

            <Flex w="100%" p={"20px"} color="white">
              <HStack w="25%" h="30px" justifyContent={"space-between"}>
                <HStack color="white">
                  <Avatar size={"sm"} src={singleBrand?.data?.logo} mr="2px" />
                  <Flex flexDir={"column"}>
                    <Text fontSize={"12px"}>{singleBrand?.data?.name}</Text>
                    <Text fontSize={"10px"} color="#999999">
                      TKN
                    </Text>
                  </Flex>
                </HStack>
                {/* <Divider orientation="vertical" color={"#9999992E"} />
                <Flex flexDir={"column"}>
                  <Text fontSize={"12px"}>$23.13</Text>
                  <Text fontSize={"10px"} color="green">
                    2.14%
                  </Text>
                </Flex> */}
                {/* <Divider orientation="vertical" />
                <Flex flexDir={"column"}>
                  <Text fontSize={"12px"}>$23,984,908</Text>
                  <Text fontSize={"10px"} color="#999999">
                    Trading volume
                  </Text>
                </Flex> */}
                <Divider orientation="vertical" />
                <Flex flexDir={"column"}>
                  <Text fontSize={"12px"}>14</Text>
                  <Text fontSize={"10px"} color="#999999">
                    Mark cap
                  </Text>
                </Flex>
                <Divider orientation="vertical" />
                <Flex flexDir={"column"}>
                  <Text fontSize={"12px"}>10,000,000</Text>
                  <Text fontSize={"10px"} color="#999999">
                    Total supply
                  </Text>
                </Flex>
              </HStack>
              <Spacer />
              <Center
                {...tokenCenter}
                as="button"
                onClick={() => {
                  tokenOverviewVisible
                    ? setTokenOverviewVisible(false)
                    : setTokenOverviewVisible(true);
                }}
              >
                <Text fontSize={"12px"}>Reward overview</Text>
                <Icon as={FaAngleDown} {...iconFlex} />
              </Center>
            </Flex>
            {/* ---------------------------------------------------HIDDEN PARTS------------------------------------------- */}
            <Stack display={tokenOverviewVisible ? "flex" : "none"}>
              {/* ----------------------------------------------------------------------SEND AND RECEIVE || DATE RANGER --------------------------------------- */}
              <HStack h={"50px"} w={"100%"} px={"20px"} pb="10px">
                <Flex {...sendFlex}>
                  <Center as="button" w={"50%"} h={"100%"} onClick={() => {}}>
                    <SendIconSvg />
                    <Text fontSize={["16px", "16px", "16px", "16px", "16px"]}>Send</Text>
                  </Center>
                  <Spacer />
                  <Center as="button" {...centerButton} onClick={() => {}}>
                    <ReceiveIconSvg />
                  </Center>
                </Flex>
                <Spacer />
                <Flex>
                  <Select
                    color={"#999999"}
                    onChange={(e: any) => {
                      setDateRange(e.target.value);
                    }}
                    value={dateRange}
                    variant="unstyled"
                    placeholder="Select range"
                    size={"sm"}
                  >
                    {["Last 30 days", "Last 7 days", "Last 3 days"]?.map((e) => (
                      <option key={nanoid()} value={e}>
                        {e}
                      </option>
                    ))}
                  </Select>
                </Flex>
              </HStack>
              {/* ---------------------------------------------GRAPH ANALYTICS---------------------------------- */}
              <Flex {...graphFlex}></Flex>
              {/* ---------------------------------------------OVERVIEW & HOW IT WORKS---------------------------------- */}
              <Flex {...overviewFlex}>
                {/* -------------------------------OVERVIEW------------------------------ */}
                <Flex w="49%" flexDir={"column"}>
                  <Heading fontSize={"16px"}>Overview</Heading>
                  <Text fontSize={"12px"} py="5px">
                    Bitcoin is the worlds first widely-adopted cryptocurrency. With Bitcoin, people
                    can securely and directly send each other digital money on the internet.
                  </Text>
                  <Text fontSize={"12px"}>
                    Ethereum, like Bitcoin, is an open source project that is not owned or operated
                    by a single individual. Anyone with an internet connection can run an Ethereum
                    node or interact with the network. Popular Ethereum-based innovations include
                    stable coins (which are pegged to the dollar by smart contract), decentralized
                    finance apps (collectively known as DeFi), and other decentralized apps (or
                    dapps). Ethereum, like Bitcoin, is an open source project that is not owned or
                    operated by a single individual. Anyone with an internet connection can run an
                    Ethereum node or interact with the network. Popular Ethereum-based innovations
                    include stable coins (which are pegged to the dollar by smart contract),
                    decentralized finance apps (collectively known as DeFi), and other decentralized
                    apps (or dapps).
                  </Text>
                </Flex>
                <Spacer />
                {/* ----------------------------HOW IT WORKS---------------------------------- */}
                <Flex w="49%" flexDir={"column"}>
                  <Heading fontSize={"16px"}>How it works</Heading>
                  <Text fontSize={"12px"} py="5px">
                    Unlike a banks ledger, the Bitcoin blockchain is distributed across the entire
                    network. No company, country, or third party is in control of it; and anyone can
                    become part of that network. There will only ever be 21 million bitcoin. This is
                    digital money that cannot be inflated or manipulated in any way.
                  </Text>
                  <Heading fontSize={"16px"}>Resources</Heading>
                  <Text fontSize={"12px"} py="5px">
                    Official website:{" "}
                    <Text as="button" decoration={"underline"}>
                      https://bitcoin.com
                    </Text>
                  </Text>
                  <Text fontSize={"12px"} py="5px">
                    Whitepaper:{" "}
                    <Text as="button" decoration={"underline"}>
                      https://arcteryx.com/newsroom/tokenconomis/whitepaper
                    </Text>
                  </Text>
                </Flex>
              </Flex>
            </Stack>
          </Stack>
          {/* ----------------------------------------------------------------------FILTERS------------------------------------------------------------ */}

          <Flex w={"100%"} borderRadius={"16px"} justifyContent="space-between">
            <HStack>
              <CategoriesFilter filterOptions={brandOffers[0]?.category?.filter_options} />
              <BrandsFilters />
              <SizesFilter />
              <PriceFilter />
            </HStack>

            <HStack>
              <SortFilter />
            </HStack>
          </Flex>

          {/* ----------------------------------------------------------------------OUTDOOR ACTIVITIES------------------------------------------------------------ */}
          {/* {offersFetching ? (
            <SkeletonLoader />
          ) : ( */}
          <SimpleGrid columns={[2, 2, 3, 4, 4]} w={"100%"} spacingX={"3.5%"}>
            {brandOffers?.map(
              // {loadedOffers?.map(
              (e: any, i: number) => (
                <DashboardProductItem
                  key={nanoid()}
                  isLoaded={true}
                  productTitle={e.name}
                  points={`${e?.tokens}${e.reward?.rewardSymbol}`}
                  productSubtitle={e.product?.category?.name}
                  price={e.originalPrice.toLocaleString()}
                  isNew={moment(moment()).diff(e?.createdAt, "days") < 1}
                  // trending={e.trending}
                  rest={{ ...e }}
                  i={i}
                  bgImage={e.offerImages}
                  onClick={() =>
                    // navigate(`${GLOBAL_PREFIX}/productDetails/${e.slug}`, {
                    //   state: { ...e },
                    // })
                    navigate(`${GLOBAL_PREFIX}/productDetails/${e.offerCode}`)
                  }
                />
              )
            )}
          </SimpleGrid>
          {/* )} */}

          {/* LOAD MORE BUTTON */}
          {/* {totalPages !== page || */}
          {!reachedEnd && (
            <Center
              as={Button}
              {...loadMore}
              onClick={() => {
                setPage(page + 1);
              }}
            >
              {offersFetching ? (
                <Spinner size={"sm"} />
              ) : (
                <>
                  Load more <Icon as={FaAngleDown} ml={"5%"} />
                </>
              )}
            </Center>
          )}
        </Stack>
      </Container>

      {/* ----------------------------------- Following BRAND INFORMATION SECTION HERE ----------------------------- */}
      <Modal
        isCentered
        blockScrollOnMount={true}
        scrollBehavior={"inside"}
        isOpen={followingModalIsOpen}
        size={"xl"}
        onClose={followingModalOnClose}
        motionPreset="slideInBottom"
        closeOnOverlayClick={false}
      >
        <ModalOverlay />
        <ModalContent
          // h={"70%"}
          overflowY={"scroll"}
          {...modalContentFollow}
        >
          <Heading fontSize={"18px"}>Follow {singleBrand?.data?.name}</Heading>

          <Wrap mt="10px">
            {followBrandOptions?.map((e, i) => (
              <Flex
                key={nanoid()}
                w={followBrandOptions.length - i === 1 ? "100%" : "48%"}
                flexDir={"column"}
                as="button"
                borderColor={selectedOfferOption === i ? "dark" : "#0000001C"}
                // h="150px"
                {...overflowFlex}
                onClick={() => setSelectedOfferOption(i)}
              >
                <HStack w="100%">
                  <Circle bg="black" p="5px">
                    <Image src={e.icon} {...circleImage} objectFit={"contain"} />
                  </Circle>
                  <Spacer />
                  {selectedOfferOption === i && (
                    <Image {...circleImage2} src={Images_Icons.blackCheckMark} />
                  )}
                </HStack>
                <Text my="5px">{e.title}</Text>
                <Text color="#999999" textAlign={"left"}>
                  {e.desc}
                </Text>
              </Flex>
            ))}
          </Wrap>

          {/* ---------------------------------------------------- BUTTON CONTROL SECTION  --------------------------------------- */}
          <Flex mt={"24px"} flexDir={"column"}>
            {/* ---------------------------- THE FOLLOW BUTTON SECTION ----------------------- */}
            <Center
              as={"button"}
              {...followCenter}
              onClick={() => {
                FollowBrand();
                followingModalOnClose();
              }}
            >
              <Text>Follow</Text>
            </Center>

            {/* ---------------------------- THE CANCEL BUTTON SECTION ----------------------- */}
            <Center as={"button"} {...cancelCenter} onClick={followingModalOnClose}>
              <Text>Cancel</Text>
            </Center>
          </Flex>
        </ModalContent>
      </Modal>

      <Footer />
    </Flex>
  );
};

export default DashboardShopFilter;

